body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.Day1{
  height: 100vh; /* Make it take up 100% of the viewport height */
  overflow-y: scroll; /* Enable vertical scrolling if content exceeds viewport height */
      
}
header{
  padding-top: 15vh;
  display: flex;
  align-items: center;
  justify-content: center;
  top: 10%;
}
@font-face {
  font-family: 'Spectre';
  src: url('/Users/maliqharris/my-app/public/SpectreRegular-MV4YB.otf') format('opentype');
}

@font-face {
  font-family: 'pixel';
  src: url('/Users/maliqharris/my-app/public/PixelMusketeer-vEXO.ttf') format('truetype');
}

  
      
  


.title{
  font-family: 'Spectre', sans-serif;
  padding-top: 5vh;
  padding-bottom: 5vh;
  padding-left: 5vh;
  padding-right: 5vh;
  backdrop-filter: blur(15px);
  border-radius: 30%;
  color: white;
   
}
.bigdiv{
  height: 100vh;
  width: 100vw;
  font-size: 5vh;
  background-image: url("/Users/maliqharris/my-app/public/final_fantasy_7_dynamic_backgrounds_gifs_3.gif");
  background-repeat: no-repeat;
  align-items: center;
  background-size: cover;
  flex-direction: column;
}
.imgdiv{
  padding-top: 20vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.imgdiv img {
  border-radius: 50%;
  width: 25%;
  height: 25%;
}
.Day2 {
  height: 100vh; /* Make it take up 100% of the viewport height */
  overflow-y: scroll; /* Enable vertical scrolling if content exceeds viewport height */
  background-image: url("/Users/maliqharris/my-app/public/20a556e32b22e1353279909890382234.gif");
  background-repeat: no-repeat;
  background-size: cover;
}
.day2title{
  color: rgb(68, 85, 180);
  height: 20vh;
  display: flex;
  align-items: center;
  justify-content: center;
  
}
.day2bottom{
  /* background-color: aliceblue; */
  height: 80vh;
  display: flex;
  flex-direction: row;

}
.day2text{
  font-family: 'Spectre', sans-serif;
  /* background-color: rgb(15, 84, 145); */
  height: 80%;
  width: 50%;
  margin-left:5vw ;
  margin-right: 5vw;
  margin-top: 8vh;
  color: rgb(88, 146, 100);
  backdrop-filter: blur(20px);
}
.day2left{
  display: flex;
  flex-direction: column;
}
.d2rt{
  height: 50%;
  width: 50vw;
  /* background-color: aqua; */
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;

}
.d2rt img{
  height: 100%;
}
.hover-image {
  display: none;
  position: absolute;
  top: 0;
  left: 20%;
}
.d2rt:hover .hover-image {
  display: block;

  

}
.d2rb{
  height: 50%;
  width: 50vw;
  /* background-color: bisque; */
  display: flex;
  justify-content: center;
  align-items: center;
}
.d2rb img{
 border-radius: 50%;
 height: 80%;
}

.Day3{
  height: 100vh;
  width: 100vw;
  background-color: aqua;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: center; */
}
.d3t{
  margin-top: 10vh;
  margin-bottom: 10vh;
  height: 10vh;
  width: 80vw;
  display: flex;
  font-family: pixel;
  align-items: center;
  justify-content: center;
  background-color: bisque;
}


/* ----------------------------------------Phone View---------------------------------------------------------------------------------- */
@media only screen and (max-width: 500px) 
{
  .imgdiv img {
    width: 200px;
    height: 200px;
  }
  .title{
    font-size: 4vw;
  }

  .day2title{
    height: 5vh;
    color: rgb(68, 85, 180);
  }
  .day2bottom{
    /* background-color: aliceblue; */
    height: 90vh;
    display: flex;
    flex-direction: column;
  
  }
  .day2text{
    font-family: 'pixel';
    /* background-color: rgb(15, 84, 145); */
    height: 10%;
    width: 100%;
    font-size: 2vh;
    color: rgb(88, 146, 100);
    backdrop-filter: blur(20px);
    margin-top: 2vh;
    margin-left: 2vh;
  }

  .day2left{
    display: flex;
    flex-direction: row;
    height: 80%;
  }

  .d2rt{
    height: 50%;
    width: 50vw;
    /* background-color: aqua; */
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
  
  }
  .d2rt img{
    height: 30vh;
    width: 45vw;
    
    margin-top: 90vh;
  }
  .d2rb img{
    border-radius: 50%;
    
    margin-top: 15vh;
    margin-right: 10vw;
 
   }
}